import React from 'react';
import './App.css';
import AppNav from "./AppNav";
import { getWeb3, getInstance}  from "./Web3Util";

import AppFooter from "./AppFooter";

var sectionStyle = {
  width: "100vw",
  backgroundRepeat: 'no-repeat',
  backgroundSize: 'cover'
};

class PlanetDetail extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      hasData: false,
      message: "",
      rows:[],
      columns: [],
      user: '',
      tokenIds: [],
      title: [],
      desc: [],
      price: [],
      publishDate: [],
      author: [],
      image: [],
      total: 0,
      contractInstance: '',
      user:'',
      selectedId:props.match.params.selectedId,
      art:null,
      priceInEther:0,
      owner:'',
      networkId:0,
      planetId: 0,
      creatorWallet:"",
      planetBirthday:"",
      index:0
    };
    this.buyPlanet = this.buyPlanet.bind(this);
    this.loadDigitalPlanet = this.loadDigitalPlanet.bind(this)
    this.loadDigitalPlanetWithID = this.loadDigitalPlanetWithID.bind(this)
  }
  resetPendingArts() {
    this.setState({ 
      tokenIds: [],
      title: [],
      desc: [],
      price: [],
      publishDate: [],
      author: [],
      image: [],
      total: 0
    });
  }



  componentDidMount = async () => {
    const web3 = await getWeb3();
    window.web3 = web3;
    const contractInstance = await getInstance(web3);
    window.user = (await web3.eth.getAccounts())[0];
    const networkId = await web3.eth.net.getId();

    this.setState({ user: window.user,networkId});
    this.setState({ contractInstance: contractInstance });
    
    await this.loadDigitalPlanet(web3);

  }

  async loadDigitalPlanetWithID(ID) {
    const web3 = await getWeb3();
    //window.location.href="/#/planet/"+ID
    let currentUrl = window.location.href 
    let tempUrl = currentUrl.split('#')[0]
    window.location.href = tempUrl + "#/planet/"+ID

    try {
      const art = await this.state.contractInstance.methods.findPlanet(ID).call();
      const owner = await this.state.contractInstance.methods.ownerOf(ID).call();
      const index = await this.state.contractInstance.methods.index().call()

      if(art != null){
        let avaxPrice =  web3.utils.fromWei(art[3], 'ether')
        this.setState({ hasData: true,art,avaxPrice,owner,
          planetId:art[0],
          creatorWallet:art[7],
          planetBirthday:art[5],
          art,
          selectedId:ID,
          index
        })
      } else {
        this.setState({ hasData: false });
      }

  } catch (e) {console.log('Error', e)}

  }
  async loadDigitalPlanet(web3) {
      try {
        const art = await this.state.contractInstance.methods.findPlanet(this.state.selectedId).call();
        const owner = await this.state.contractInstance.methods.ownerOf(this.state.selectedId).call();
        const index = await this.state.contractInstance.methods.index().call()

        if(art != null){
          let avaxPrice =  web3.utils.fromWei(art[3], 'ether')
          this.setState({ hasData: true,art,avaxPrice,owner,
            planetId:art[0],
            creatorWallet:art[7],
            planetBirthday:art[5],
            index
          })
        } else {
          this.setState({ hasData: false });
        }
 
    } catch (e) {console.log('Error', e)}
  
  }
  async buyPlanet(tokenId, priceInEther) {
    try {
      const priceInWei =  window.web3.utils.toWei(priceInEther, 'ether');
      await this.state.contractInstance.methods.buyPlanet(tokenId).send({
          from: this.state.user, value: priceInWei
      })
      window.location.reload(); 
    } catch (e) {console.log('Error', e)}
  };

  getCurrency(networkId){
    let currency = ""
    if(networkId == 1){
        currency = "$AVAX"
    }else if(this.state.networkId == 56){
        currency = "$BNB"
    }else if(this.state.networkId == 250){
        currency = "$FTM"
    }else if(this.state.networkId == 1666600000){
        currency = "$ONE"
    }
    return currency
}   

getPrice(networkId){
    let  price =0
    if(networkId == 1){
        price = "1"
    }else if(this.state.networkId == 56){
        price = "0.05"
    }else if(this.state.networkId == 250){
        price = "50"
    }else if(this.state.networkId == 1666600000){
        price = "200"
    }
    return price
}

  render() {
    if (this.state.hasData) {
      let style = {background:"white",paddingBottom:"15px",borderRadius:"55px"}
      
      let networkId, generationNumber, planetId, creatorWallet, planetBirthday, finalString

      networkId = this.state.networkId
      generationNumber = 1
      planetId = this.state.planetId
      creatorWallet = this.state.creatorWallet
      planetBirthday = this.state.planetBirthday
      finalString = networkId+"-"+generationNumber+"-"+planetId+"-"+creatorWallet+"-"+planetBirthday

      console.log("@@@",finalString)
      let hasNext = parseInt(this.state.art[0]) < parseInt(this.state.index) -1
      let next =parseInt(this.state.art[0]) +1
      let network

      return (
        <div className="App" style={sectionStyle}>
          <AppNav></AppNav>
          <section className="text-center">

       
            <div className="container">
                <div className="row" style={{marginTop:"50px"}}  >
                <div className="col-lg-12 col-md-12 mb-lg-0 mb-0"></div>
                    <div className="col-lg-12 col-md-12 mb-lg-0 mb-0">
                            <div style={style}>
                            <div className="view overlay rounded z-depth-2 mb-2">
                              {/*<img className="img-fluid" src={this.state.art[9]} alt="Sample"/>*/}
                              {/* this is for local
                              <iframe style={{width:"100%",height:"500px",marginBottom:"-15px"}} src={"http://localhost:8089/planet.html?seed="+finalString} /> */}
                              <iframe style={{width:"100%",height:"500px",marginBottom:"-15px"}} src={"https://cosmicroulette.app/planet.html?seed="+finalString} />


                            </div>
                            <h5 className="font-weight-bold mb-1 marginTopC">Planet ID: {this.state.art[0]}</h5>
                            <div className=" text20"> <span className="font-weight-bold">Name: {this.state.art[1]}</span></div>

                            <div className="text20"> <span className="font-weight-bold">Created by: {this.state.art[6]}</span></div>
                            <div className="text20"> <span className="font-weight-bold">Price: {this.state.avaxPrice} {this.getCurrency(this.state.networkId)} </span></div>

                            {/*<p style={{whiteSpace: 'nowrap',overflow: 'hidden',textOverflow:'ellipsis'}}>Price: {this.state.avaxPrice} {this.getCurrency(this.state.networkId)} </p>*/}
                            {/*<p style={{whiteSpace: 'nowrap',overflow: 'hidden',textOverflow:'ellipsis'}}>by <span className="font-weight-bold">{this.state.art[6]}</span>, {this.state.art[5]}</p>
                            
                            <p className="alert alert-primary dark-grey-text text20">{this.state.art[2]}</p>*/}
                           {this.state.art[0] >0?
                            <a onClick={()=>{this.loadDigitalPlanetWithID(this.state.art[0]-1)}}>
                            <button className="btn btn-blue btn-rounded btn-md" >See previous</button>
                             </a>
                          :null
                          }
                           
                            {this.state.art.status ==1?

                              <>
                                {this.state.owner !=  window.user ? 
                                  <button className="btn btn-green btn-rounded btn-md" onClick={e => (e.preventDefault(),this.buyPlanet(this.state.art[0], this.state.avaxPrice))}>Collect NFT</button>
                              
                                :null }
                              </>

                            :null}
                            {hasNext?
                            <a onClick={()=>{this.loadDigitalPlanetWithID(next)}}>
                            <button className="btn btn-blue btn-rounded btn-md" >See next</button>
                             </a>
                          :null
                          }
                        </div>
                    </div>
                    <div className="col-lg-12 col-md-12 mb-lg-0 mb-0"></div>
                </div>
              </div>
              <div style={{color:"white"}}className="h1-responsive  font-weight-bold text-center my-5">
              Creator:<br/><span className="detail">{this.state.art[7]}</span><br/>
              Current owner:<br/><span className="detail"> {this.state.owner}</span><br/>
              Share URL:<br/><span className="detail"> { window.location.href}</span><br/>
              

            </div>
          </section>
        </div>
      );
    } else {
      return (
        <div className="App" style={sectionStyle}>
          <AppNav></AppNav>
          <section className="text-center">

          <div  style={{color:"white"}} className="h1-responsive normal font-weight-bold text-center my-5">
            Create and trade Digital Planets on Cosmic Roulette 
          </div>
          <p  style={{color:"white"}}  className="h1-responsive  normal font-weight-bold text-center my-5">
          All the planet data on Cosmic Roulette are on the blockchain
          </p>


          <div className="container">
              <div className="alert alert-primary" role="alert">
                You need metamask installed and configured on Avalanche c-chain, Fantom network or Binance Smart Chain to use this site! Learn how to do it by checking the links below. 
              </div>
              <p ><a className="customA" href="https://docs.avax.network/build/tutorials/smart-contracts/deploy-a-smart-contract-on-avalanche-using-remix-and-metamask" target="_blank">How to change metamask Network to Avax C-Chain Mainnet?</a></p>
              <p ><a className="customA" href="https://docs.avax.network/build/tutorials/platform/transfer-avax-between-x-chain-and-c-chain" target="_blank">Need help transferring $AVAX to c-chain?</a></p>
              
              <p ><a className="customA" href="https://academy.binance.com/en/articles/connecting-metamask-to-binance-smart-chain" target="_blank">How to change metamask Network to Binance Smart Chain?</a></p>
              <p ><a className="customA" href="https://crypto-explained.com/services/send-bnb-coin-to-binance-smart-chain-on-metamask/" target="_blank">Need help transferring $BNB to metamask?</a></p>
              
              <p ><a className="customA" href="https://docs.fantom.foundation/tutorials/set-up-metamask" target="_blank">How to change metamask Network to Fantom Mainnet?</a></p>

              </div>
          </section> <br/><br/><br/>
        </div>
      );
    }

  }
}

export default PlanetDetail;
