import React from "react";
import { withRouter,Link} from 'react-router-dom';
import "./App.css";

class AppFooter extends React.Component {
    constructor(props) {
      super(props);
      this.handleNavClick = this.handleNavClick.bind(this);
      this.state = { 
            name: '',
            symbol: ''
        };
    }
    handleNavClick = param => e => {
      e.preventDefault();
      this.props.history.push('/'+param)
    };

  render() {
    return (
      <nav id="footer" className="navbar navbar-expand-lg navbar-dark" style={{borderTop:"1px solid #ffffff59",height:"110px",paddingBottom:"20px",background:"#0c0f13", ...this.props.style}}>
        
        <div style={{margin:"auto"}}>
            <Link  className="btn btn-outline-white btn-md my-2 my-sm-0 ml-3" to="/explained">
            Cosmic Roulette explained  </Link>
          <Link  className="btn btn-outline-white btn-md my-2 my-sm-0 ml-3" to="/terms">
            Terms and conditions </Link>
        </div>

      </nav>
    );
  }
}
export default withRouter(AppFooter);
