import React from 'react';
import './App.css';
import AppNav from "./AppNav";
import axios from 'axios';
import { getWeb3, getInstance}  from "./Web3Util";
import AppFooter from "./AppFooter";

var sectionStyle = {
    width: "100vw",
    minHeight:"110vh",
    paddingBottom:"150px",
    backgroundRepeat: 'no-repeat',
    backgroundSize: 'cover'
};

class Explained extends React.Component {
    constructor(props) {
      super(props);
     
    }


render() {


    return (
        <div style={sectionStyle}>
          <AppNav></AppNav>
          <section className="mx-auto" style={{ marginTop: '20px'}}>
              <div className="row">
                  <div className="col-md-2 mb-md-0 mb-5"></div>
                  <div className="col-md-8 mb-md-0 mb-5">
                      <div className="card">
                          <div className="card-body">
                              <form className="text-center border border-light p-5" >
                                  <p className="h4 mb-4"><i>Cosmic Roulette explained</i> </p>
                                  <div className="row">
                                        <div className="col-md-3"></div>
                                      <div className="col-md-6 mb-md-0 mb-5">
                                      Cosmic Roulette is a decentralized application running on many blockchains. <br/>
                                      For the first generation, it will be possible to mint 10.000 planets for each one of the available blockchains (Binance Smart Chain, Fantom and Avalanche).<br/><br/>
                                      Each and every planet is provably unique, given that planets are genereted using the following data as a seed: <br /><br />
                                      <b>The creator's wallet address</b> <br/>
                                       <b> the planet's birthdate</b><br />
                                       <b> its generation number </b><br />
                                        <b> its unique ID</b><br />
                                        <b>the current blockchain ID</b><br /> <br/>

                                        In order to create a planet, a user must pay a fee. All other interactions in the website only charge users for their gas cost. Users can mint as many planets they want, before the 10.000 planet threshold is reached for the current blockchain. <br /><br />
                                        Price to mint one planet: <br/> <br/> 
                                        <b>Binance Smart chain: 0.05 $BNB</b> <br/>
                                       <b> Fantom: 50 $FTM</b><br />
                                       <b> Avalanche: 1 $AVAX </b><br /><br/><br/>

                                        After minting a planet, it is automatically put for sale using the price chosen by the user in the "Create your NFT page". <br/>
                                        Once a user creates or purchases a planet, it can be found under "My universe" tab. <br/>
                                        
                                       
                                       </div>
                                        <div className="col-md-3"></div>
                                      
                                  </div>
                    
                              </form>
                          </div>
                      </div>
                  </div>
                  <div className="col-md-2 mb-md-0 mb-5"></div>
                  
              </div>

          </section>
      </div>

    );
  }
}
export default Explained;