import React from "react";
import {  Route, Switch ,HashRouter} from "react-router-dom";

import PlanetHome from './PlanetHome';
import CreatePlanet from './CreatePlanet';
import MyWallet from './MyWallet';
import PlanetDetail from './PlanetDetail';
import Terms from './Terms';
import Explained from './Explained';
import AppFooter from "./AppFooter";


class Routes extends React.Component {
  render() {
    return (
      <HashRouter>
      <Switch>
        <Route exact path="/" component={PlanetHome} />
        <Route exact path="/home" component={PlanetHome} />

        <Route exact path="/rollNft" component={CreatePlanet} />
        <Route exact path="/myWallet" component={MyWallet} />
        <Route exact path="/terms" component={Terms} />
        <Route exact path="/explained" component={Explained} />


        <Route  path="/planet/:selectedId" component={PlanetDetail} />
      <Route
        render={function() {
          return <h1>Not Found</h1>;
        }}
      />
    </Switch>
    <AppFooter />

    </HashRouter>
    );
  }
}

export default Routes;
