import Web3 from 'web3';
import DigitizedPlanetAVAX from './abi/DigitizedPlanetAVAX.json'
import DigitizedPlanetONE from './abi/DigitizedPlanetONE.json'
import DigitizedPlanetBSC from './abi/DigitizedPlanetBSC.json'
import DigitizedPlanetFTM from './abi/DigitizedPlanetFTM.json'


export const getWeb3 = () =>
    new Promise(async(resolve, reject) => {
    //window.addEventListener("load", async () => {
    if (window.ethereum) {
        const web3 = new Web3(window.ethereum);
        try {
        await window.ethereum.enable();
        resolve(web3);
        } catch (error) {
        reject(error);
        }
    } else if (window.web3) {
        // load metamask provider
        const web3 = window.web3;
        console.log("Injected web3 detected.");
        resolve(web3);
    } else {
        console.log(process.env.PUBLIC_URL)
        const provider = new Web3.providers.HttpProvider("http://127.0.0.1:7545");
        const web3 = new Web3(provider);
        console.log("No web3 instance injected, using Local web3.");
        resolve(web3);
    }
    //});
});
export const getInstance = async (web3) => {
    const networkId = await web3.eth.net.getId();
    window.user = (await web3.eth.getAccounts())[0];

    //if avalanche 
    if(networkId ==1){
        const deployedNetwork = DigitizedPlanetAVAX.networks[networkId];
    
        window.instance = new web3.eth.Contract(
            DigitizedPlanetAVAX.abi,
            deployedNetwork && deployedNetwork.address,
            {
                from: window.user
            }
        );
        return window.instance;
    }
    //for BSC
    else if(networkId ==56){
        const deployedNetwork = DigitizedPlanetBSC.networks[networkId];
        window.instance = new web3.eth.Contract(
            DigitizedPlanetBSC.abi,
            deployedNetwork && deployedNetwork.address,
            {
                from: window.user
            }
        );
        return window.instance;
    }
     //for fantom
     else if(networkId ==250){
        const deployedNetwork = DigitizedPlanetFTM.networks[networkId];
        window.instance = new web3.eth.Contract(
            DigitizedPlanetFTM.abi,
            deployedNetwork && deployedNetwork.address,
            {
                from: window.user
            }
        );
        return window.instance;
    }
      //for harmony
      else if(networkId ==1666600000){
        const deployedNetwork = DigitizedPlanetONE.networks[networkId];
        window.instance = new web3.eth.Contract(
            DigitizedPlanetONE.abi,
            deployedNetwork && deployedNetwork.address,
            {
                from: window.user
            }
        );
        return window.instance;
    }
    else{
        return window.instance;
    }
    
}