import React from 'react';
import './App.css';
import AppNav from "./AppNav";
import axios from 'axios';
import { getWeb3, getInstance}  from "./Web3Util";
import AppFooter from "./AppFooter";

var sectionStyle = {
    width: "100vw",
    minHeight:"110vh",
    paddingBottom:"150px",
    backgroundRepeat: 'no-repeat',
    backgroundSize: 'cover'
};

class CreatePlanet extends React.Component {
    constructor(props) {
      super(props);
      this.imageChange = this.imageChange.bind(this);
      this.submitHandler = this.submitHandler.bind(this);
      this.changeHandler = this.changeHandler.bind(this);
      this.state = { 
            imageValue: 'NA',
            description: 'NA',
            title: '', 
            authorName: '',
            imageUrl:'NA',
            price: null,
            date:'',
            user: '',
            balance: 0,
            contractInstance: '',
            networkId:'',
            networkType:'',
            selectedFile: null,
        };
    }
    componentDidMount = async () => {
        const web3 = await getWeb3();
        window.web3 = web3;
        const contractInstance = await getInstance(web3);
        window.user = (await web3.eth.getAccounts())[0];
        const balanceInWei = await web3.eth.getBalance(window.user);
        var balance = web3.utils.fromWei(balanceInWei, 'ether');
        const networkId = await web3.eth.net.getId();
        const networkType = await web3.eth.net.getNetworkType();
        this.setState({ user: window.user });
        this.setState({ balance: balance});
        this.setState({ contractInstance: contractInstance });
        this.setState({ networkId: networkId});
        this.setState({ networkType: networkType});
      }
    imageChange = (event) => {
        this.setState({ imageValue: event.target.value });
    };
    categoryChange = (event) => {
        this.setState({ categoryValue: event.target.value });
    };
    submitHandler = (event) => {
        event.preventDefault();
        const {  imageValue, description, title, authorName, price, date,imageUrl} = this.state;
        let localPrice = 0
        if(price != null){
            localPrice = price
        }
        if(this.isNotEmpty(title) &&this.isNotEmpty(description) &&this.isNotEmpty(authorName) 
            &&this.isNotEmpty(date)&&this.isNotEmpty(imageUrl) && this.isNotEmpty(localPrice) && imageUrl !== "images/upload-file.png") {
            const priceInWei =  window.web3.utils.toWei(localPrice, 'ether');
            this.publishArt(title, description, date, authorName, priceInWei, imageUrl);  
        }
    };
    isNotEmpty(val) {
        return val&& val.length>0;
    }
    changeHandler = event => {
        this.setState({
            [event.target.name]: event.target.value
            }, function(){ })
    };

    onClickHandler = () => {
        const data = new FormData()
        data.append('file', this.state.selectedFile)
        axios.post("http://localhost:8000/upload", data, { 
           // receive two    parameter endpoint url ,form data
       }).then(res => { // then print response status
        console.log(res.statusText)
        this.setState({ imageValue: "images/" + this.state.selectedFile.name });
     })
    }

    async publishArt(title, description, date, authorName, price, imageValue) {
        try {
            //check network
            //add correct price for network
            const priceInWei =  window.web3.utils.toWei(this.getPrice(this.state.networkId), 'ether');

            await this.state.contractInstance.methods.createTokenAndSellPlanet(title,description, date, authorName, price, imageValue).send({
                from: this.state.user, value:priceInWei
            })
            let myPlanets = await this.state.contractInstance.methods.findMyPlanets().call()
            if(myPlanets.length>0){
                this.props.history.push(`/planet/`+myPlanets[myPlanets.length-1])
                window.location.reload(); 
            }
        } catch (e) {console.log('Error', e)}
    }


getCurrency(networkId){
    let currency = ""
    if(networkId == 1){
        currency = "$AVAX"
    }else if(this.state.networkId == 56){
        currency = "$BNB"
    }else if(this.state.networkId == 250){
        currency = "$FTM"
    }else if(this.state.networkId == 1666600000){
        currency = "$ONE"
    }
    return currency
}   

getPrice(networkId){
    let  price =0
    if(networkId == 1){
        price = "1"
    }else if(this.state.networkId == 56){
        price = "0.05"
    }else if(this.state.networkId == 250){
        price = "50"
    }else if(this.state.networkId == 1666600000){
        price = "200"
    }
    return price
}

render() {

    

    return (
        <div style={sectionStyle}>
          <AppNav></AppNav>
          <section className="mx-auto" style={{ marginTop: '20px'}}>
              <div className="row">
                  <div className="col-md-2 mb-md-0 mb-5"></div>
                  <div className="col-md-8 mb-md-0 mb-5">
                      <div className="card">
                          <div className="card-body">
                              <form className="text-center border border-light p-5" onSubmit={this.submitHandler}>
                                  <p className="h4 mb-4">A <i>unique</i> planet will be created using the information below</p>
                                  <div className="row">
                                        <div className="col-md-3"></div>
                                      <div className="col-md-6 mb-md-0 mb-5">
                                          <input className="form-control mb-4" id="title" name="title" type="text" placeholder="Planet name" onChange={this.changeHandler}  value={this.state.title}/>
                                        {/* <input className="form-control mb-4" id="description" name="description"  type="text" placeholder="Description" onChange={this.changeHandler}  value={this.state.description}/> */} 
                                         <input className="form-control mb-4" id="authorName" name="authorName" type="text" placeholder="Author signature" onChange={this.changeHandler}  value={this.state.authorName}/>
                                         {/*<input className="form-control mb-4" id="imageUrl" name="imageUrl" type="text" placeholder="NFT URL" onChange={this.changeHandler}  value={this.state.imageUrl}/>*/} 
                                         <input className="form-control mb-4" id="price" name="price" type="text" placeholder={"Planet sale price in "+this.getCurrency(this.state.networkId)} onChange={this.changeHandler} value={this.state.price} />
                                            <input className="form-control mb-4" id="date" name="date" type="date" placeholder="PLanet birthday" onChange={this.changeHandler} value={this.state.date} />
                                      </div>
                                        <div className="col-md-3"></div>
                                      
                                  </div>
                             
                                  <br></br><br></br>
                                  <div className="row">
                                      <div className="col-md-3 mb-md-0 mb-5"></div>
                                      <div className="col-md-6 mb-md-0 mb-5"><button className="btn btn-info" type="submit">Create your planet for {this.getPrice(this.state.networkId)} {this.getCurrency(this.state.networkId)}</button></div>
                                      <div className="col-md-3 mb-md-0 mb-5"></div>

                                  </div>
                    
                              </form>
                          </div>
                      </div>
                  </div>
                  <div className="col-md-2 mb-md-0 mb-5"></div>
                  
              </div>

          </section>

      </div>

    );
  }
}
export default CreatePlanet;