import React from "react";
import { withRouter,Link} from 'react-router-dom';
import "./App.css";
import { getWeb3, getInstance}  from "./Web3Util";

class AppNav extends React.Component {
    constructor(props) {
      super(props);
      this.handleNavClick = this.handleNavClick.bind(this);
      this.state = { 
            name: '',
            symbol: ''
        };
    }
    handleNavClick = param => e => {
      e.preventDefault();
      this.props.history.push('/'+param)
    };
    componentDidMount = async () => {

      const web3 = await getWeb3();
      const contractInstance = await getInstance(web3);
      window.user = (await web3.eth.getAccounts())[0];
      const symbol = await contractInstance.methods.symbol().call()

      this.setState({ symbol: symbol });
            const name = await contractInstance.methods.name().call();

      this.setState({ name: name });
  }

  render() {
    return (
      <nav className="navbar navbar-expand-lg navbar-dark stylish-color" style={{minHeight:"125px"}}>
        <div className="navbar-brand">  
        <Link  id="logo" className="navbar-item text-white" to="/">
          
                Cosmic Roulette 
              </Link>
        </div> 
      <form className="form-inline  my-2 my-lg-0 ml-auto" style={{marginRight:"15px"}}>
        <Link  className="btn btn-outline-white btn-md my-2 my-sm-0 ml-3" to="/">
        Roll random planet </Link>
        <Link style={{backgroundColor:"#388e3c"}}  className="btn btn-outline-white btn-md my-2 my-sm-0 ml-3" to="/rollNft">
        Create your planet  </Link>
      <Link  className="btn btn-outline-white btn-md my-2 my-sm-0 ml-3" to="/myWallet">
        My universe </Link>

        <a href="https://twitter.com/roulette_cosmic"  target="_blank" className="btn btn-outline-white btn-md my-2 my-sm-0 ml-3">
          <svg style={{fill:"white",marginTop:"-15px",marginBottom:"-15px",height:"30px"}} className="w-full fill-current h-full" version="1.1" id="Capa_1" xmlns="http://www.w3.org/2000/svg" x="0px" y="0px" viewBox="0 0 512 512"><g><g><path d="M512,97.248c-19.04,8.352-39.328,13.888-60.48,16.576c21.76-12.992,38.368-33.408,46.176-58.016 c-20.288,12.096-42.688,20.64-66.56,25.408C411.872,60.704,384.416,48,354.464,48c-58.112,0-104.896,47.168-104.896,104.992 c0,8.32,0.704,16.32,2.432,23.936c-87.264-4.256-164.48-46.08-216.352-109.792c-9.056,15.712-14.368,33.696-14.368,53.056 c0,36.352,18.72,68.576,46.624,87.232c-16.864-0.32-33.408-5.216-47.424-12.928c0,0.32,0,0.736,0,1.152 c0,51.008,36.384,93.376,84.096,103.136c-8.544,2.336-17.856,3.456-27.52,3.456c-6.72,0-13.504-0.384-19.872-1.792 c13.6,41.568,52.192,72.128,98.08,73.12c-35.712,27.936-81.056,44.768-130.144,44.768c-8.608,0-16.864-0.384-25.12-1.44 C46.496,446.88,101.6,464,161.024,464c193.152,0,298.752-160,298.752-298.688c0-4.64-0.16-9.12-0.384-13.568 C480.224,136.96,497.728,118.496,512,97.248z"></path></g></g><g></g><g></g><g></g><g></g><g></g><g></g><g></g><g></g><g></g><g></g><g></g><g></g><g></g><g></g><g></g></svg>
        </a>
        </form>
      </nav>
    );
  }
}
export default withRouter(AppNav);
