import React from 'react';
import './App.css';
import AppNav from "./AppNav";
import AppFooter from "./AppFooter";

import { getWeb3, getInstance}  from "./Web3Util";
import { Link} from "react-router-dom";


var sectionStyle = {
  width: "100vw",
  minHeight:"100vh",
  backgroundSize: 'cover',
  backgroundAttachment: 'fixed'
};

class PlanetHome extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      hasData: false,
      message: "",
      rows:[],
      columns: [],
      user: '',
      tokenIds: [],
      title: [],
      desc: [],
      price: [],
      publishDate: [],
      author: [],
      image: [],
      total: 0,
      contractInstance: '',
      user:'',
      networkId:0,
      planetId: 0,
      creatorWallet:"",
      planetBirthday:"",
      status:0,
      owner:'',
      index:0
    };
    this.buyPlanet = this.buyPlanet.bind(this);
  }
  resetPendingArts() {
    this.setState({ 
      tokenIds: [],
      title: [],
      desc: [],
      price: [],
      publishDate: [],
      author: [],
      image: [],
      total: 0
    });
  }
  componentDidMount = async () => {
    const web3 = await getWeb3();
    window.web3 = web3;
    const contractInstance = await getInstance(web3);
    window.user = (await web3.eth.getAccounts())[0];
    const networkId = await web3.eth.net.getId();
    
    this.setState({ user: window.user,networkId });
    this.setState({ contractInstance: contractInstance });
    await this.loadDigitalArts(web3);

  }
  async loadDigitalArts(web3) {
      try {
        let ids;
        const result = await this.state.contractInstance.methods.findAllPendingPlanets().call();
        let planetCounter = await this.state.contractInstance.methods.index().call();

        this.setState({
          index:planetCounter
        })
        ids = result[0];
        let newIds = [];

        for(let counter =0;counter<ids.length;++counter){
            newIds[newIds.length]=ids[counter];
        }
        // only show last planet
        let randomID =  newIds[Math.floor(Math.random() * newIds.length)];

        newIds.splice(1);
        newIds[0] = randomID

        let _total = newIds.length;
        if(newIds && _total>0) {
          let row;
          if(_total<=3) {
            row = 1;
          } else {
            row = Math.ceil(_total/3);
          }
          let columns = 3;
          this.setState({ rows: [], columns: [] });
          let rowArr = Array.apply(null, {length: row}).map(Number.call, Number);
          let colArr = Array.apply(null, {length: columns}).map(Number.call, Number);
          this.setState({ rows: rowArr, columns: colArr });
          let _tokenIds= [], _title =[],  _desc= [], _price= [], _publishDate= [],  _image =[], _author=[],_status=[];
          let idx =0;
          this.resetPendingArts();
          for(let i = 0; i<row; i++) {
            for(let j = 0; j < columns; j++) {
               if(idx<_total) {
                 let tokenId= newIds[idx];
                 const art = await this.state.contractInstance.methods.findPlanet(tokenId).call();
                 const owner = await this.state.contractInstance.methods.ownerOf(tokenId).call();

                 const priceInEther = web3.utils.fromWei(art[3], 'ether');
                  /*
                  id: index,
                  title: _title,
                  description: _description,
                  price: _price,
                  date: _date,
                  authorName: _authorName,
                  author: msg.sender,
                  owner: msg.sender,
                  status: 1,
                  image: _image
                  */
                 _tokenIds.push(art[0]);
                 _title.push(art[1]);
                 _desc.push(art[2]);
                 _price.push(priceInEther);
                 _publishDate.push(art[5]);
                 _status.push(art.status);
                 _image.push(art[9]);
                 _author.push(art[6]);
                 this.setState({
                   planetId:art[0],
                   creatorWallet:art[7],
                   planetBirthday:art[5],
                   owner
                 })

               }
              idx++;
            }
        }
         
          this.setState({ 
            tokenIds: _tokenIds,
            title: _title,
            desc: _desc,
            price: _price,
            publishDate: _publishDate,
            author: _author,
            image: _image,
            total: _total,
            status:_status
          });
          this.setState({ hasData: true });
        } else {
          this.setState({ hasData: false });
        }
 
    } catch (e) {console.log('Error', e)}
  
  }
  async buyPlanet(tokenId, priceInEther) {
    try {

      const priceInWei =  window.web3.utils.toWei(priceInEther, 'ether');
      await this.state.contractInstance.methods.buyPlanet(tokenId).send({
          from: this.state.user, value: priceInWei
      })
      window.location.reload(); 
    } catch (e) {console.log('Error', e)}
  };

  
  getStyle(id){
    
    let style = {background:"white",paddingBottom:"15px",borderRadius:"55px"}

    return style
  }


  getCurrency(networkId){
    let currency = ""
    if(networkId == 1){
        currency = "$AVAX"
    }else if(this.state.networkId == 56){
        currency = "$BNB"
    }else if(this.state.networkId == 250){
        currency = "$FTM"
    }else if(this.state.networkId == 1666600000){
        currency = "$ONE"
    }
    return currency
}   

getPrice(networkId){
    let  price =0
    if(networkId == 1){
        price = "1"
    }else if(this.state.networkId == 56){
        price = "0.05"
    }else if(this.state.networkId == 250){
        price = "50"
    }else if(this.state.networkId == 1666600000){
        price = "200"
    }
    return price
}

  render() {
    if (this.state.hasData) {

      let networkId, generationNumber, planetId, creatorWallet, planetBirthday, finalString

      networkId = this.state.networkId
      generationNumber = 1
      planetId = this.state.planetId
      creatorWallet = this.state.creatorWallet
      planetBirthday = this.state.planetBirthday

      finalString = networkId+"-"+generationNumber+"-"+planetId+"-"+creatorWallet+"-"+planetBirthday
      console.log("@@@",finalString)

      return (
        <div className="App" style={sectionStyle}>
          <AppNav ></AppNav>
          <section className="text-center">
            
            {this.state.index >0?
            <div style={{color:"white"}}className="h1-responsive font-weight-bold text-center my-5">
              {10000-this.state.index} planets left to be created
            </div>
          
          :null}
          

          <div style={{color:"white",fontSize:"24px"}}className="h1-responsive font-weight-bold text-center my-5">
            Random planet in the current blockchain 
          </div>
       
            <div className="container">
               {this.state.rows.map((row, i) =>
                <div className="row"  key={i}>
                  {this.state.columns.map((col, j) =>
                    <div  style={{marginTop:"50px"}} className="col-lg-12 col-md-12 mb-lg-0 mb-0" key={j}>
                        { i*3+j < this.state.total &&
                            <div style={this.getStyle(this.state.tokenIds[i*3+j])}>
                            <div className="view overlay rounded z-depth-2 mb-2">
                             <a href={"/#/art/"+this.state.tokenIds[i*3+j]} >
                               {/* this is for local 
                               <iframe style={{width:"100%",height:"500px",marginBottom:"-15px"}} src={"http://localhost:8089/planet.html?seed="+finalString} />*/}
                                <iframe style={{width:"100%",height:"500px",marginBottom:"-15px"}} src={"https://cosmicroulette.app/planet.html?seed="+finalString} />
                              </a>
                            </div>
                            <h5 className="font-weight-bold mb-1 marginTopC"><Link to={"/planet/"+this.state.tokenIds[i*3+j]}>Planet #{Number.parseInt(this.state.tokenIds[i*3+j])} out of 10.000</Link>, {this.state.title[i*3+j]}</h5>
                           {/**  <p style={{whiteSpace: 'nowrap',overflow: 'hidden',textOverflow:'ellipsis'}}>by <span className="font-weight-bold">@{this.state.author[i*3+j]}</span></p>*/}
                         
                        {this.state.status ==1?
                           <>
                           {this.state.owner !=  window.user ? 

                          <button className="btn btn-green btn-rounded btn-md" onClick={e => (e.preventDefault(),this.buyPlanet(this.state.tokenIds[i*3+j], this.state.price[i*3+j]))}>Collect this planet for  {this.state.price[i*3+j]} {this.getCurrency(this.state.networkId)}</button>
                          :null}
                          </>
                      :null}
                           {/*<Link to={"/rollNft"}> <button className="btn btn-red btn-rounded btn-md">Create a brand new NFT for X $AVAX</button></Link>*/}

                        </div>
                        }
                    </div>

                  )}
                </div>
              )}
              </div>
          </section>

        </div>
      );
    } else {
      return (
        <div className="App" style={sectionStyle} >
          <AppNav></AppNav>
          <section className="text-center" style={{minHeight:"71vh"}}>

            <div  style={{color:"white"}} className="h1-responsive normal font-weight-bold text-center my-5">
              Create and trade unique Digital Planets on Cosmic Roulette 
            </div>
     
            <div className="container">
              <div className="alert alert-primary" role="alert">
                You need metamask installed and configured on Avalanche c-chain, Fantom network or Binance Smart Chain to use this site! Learn how to do it by checking the links below. 
              </div>
              <p ><a className="customA" href="https://docs.avax.network/build/tutorials/smart-contracts/deploy-a-smart-contract-on-avalanche-using-remix-and-metamask" target="_blank">How to change metamask Network to Avax C-Chain Mainnet?</a></p>
              <p ><a className="customA" href="https://docs.avax.network/build/tutorials/platform/transfer-avax-between-x-chain-and-c-chain" target="_blank">Need help transferring $AVAX to c-chain?</a></p>
              
              <p ><a className="customA" href="https://academy.binance.com/en/articles/connecting-metamask-to-binance-smart-chain" target="_blank">How to change metamask Network to Binance Smart Chain?</a></p>
              <p ><a className="customA" href="https://crypto-explained.com/services/send-bnb-coin-to-binance-smart-chain-on-metamask/" target="_blank">Need help transferring $BNB to metamask?</a></p>
              
              <p ><a className="customA" href="https://docs.fantom.foundation/tutorials/set-up-metamask" target="_blank">How to change metamask Network to Fantom Mainnet?</a></p>

            

              </div>
          </section> <br/><br/><br/>
        </div>
      );
    }

  }
}

export default PlanetHome;
